	module.exports = {"data":{"markdownRemark":{"html":"<p>HackerOne offers a bi-directional Jira integration that syncs information between your HackerOne report and the Jira issue.</p>\n<p>To integrate with Jira Server:</p>\n<ol>\n<li>\n<p>Go to <strong>Settings > Program > Integrations</strong>.</p>\n</li>\n<li>\n<p>Click the <strong>Connect with JIRA</strong> link.\n<img src=\"./images/jira-server-1.png?raw=true\" alt=\"jira-server-1\"></p>\n</li>\n<li>\n<p>Choose <strong>JIRA Server</strong>, and you'll be redirected to this setup screen where you can configure your Jira integration.\n<img src=\"./images/jira-server-3.png?raw=true\" alt=\"jira-server-3\"></p>\n<p><img src=\"./images/jira-server-2.png?raw=true\" alt=\"jira-server-2\"></p>\n</li>\n<li>\n<p>Enter the URL of the Jira instance you want to authenticate with in the <strong>Instance base URL</strong> field.</p>\n</li>\n<li>\n<p>Click the <strong>Authenticate with JIRA</strong> button to finalize the set up between HackerOne and your Jira instance.  </p>\n</li>\n<li>\n<p>(Optional) Configure which Jira events you want to post to your HackerOne report. You have full control over which events you want to enable and disable.</p>\n</li>\n</ol>\n<p>You're all set! Now that you've finished setting up the Jira integration, you can create Jira issues right from your HackerOne report.</p>\n<h3>How the Integration Works</h3>\n<p>To create Jira issues from your HackerOne report:\n[ask martijn for JIRA server screenshots]</p>","frontmatter":{"path":"/programs/jira-server-integration","title":"Jira Server Integration"}}},"pathContext":{}}

